<template>
  <div class="hot-follow-router-container">
    <div class="main-card">
      <div class="main-header">
        <span class="main-title">热点关注</span>
        <CloseBtn routeName="Home" />
      </div>
      <div class="hot-follow-router-body">
        <vue-seamless-scroll :data="textDataList" class="seamless-warp" :class-option="classOption">
          <a :href="item.website" target="_blank" class="content" v-for="(item,index) in textDataList" :key="index">
            <div class="lineLeft1"></div>
            <div class="imgLeft"></div>
            <div class="lineLeft2"></div>
            <div class="textRight">
              {{item.title}}
            </div>
          </a>
          <!-- <a :href="item.website" target="_blank" class="content" v-for="(item,index) in textDataList" :key="index">{{item.title}}</a> -->
        </vue-seamless-scroll>
      </div>

    </div>

  </div>
</template>

<script>
import { http } from "@/utils/request";
import CloseBtn from "@/components/CloseBtn";
export default {
  components: {
    CloseBtn,
  },
  data() {
    return {
      textDataList: [],
      details: null,
    };
  },
  created() {
    this.details = this.$route.query.details;
    this.requestTestData();
  },
  watch: {
    $route(newVla) {
      this.details = newVla.query.details;
      this.requestTestData();
    },
  },
  computed: {
    classOption() {
      return {
        step: 0.5, // 数值越大速度滚动越快
        // limitMoveNum: 10, // 开始无缝滚动的数据量 this.dataList.length
        hoverStop: true, // 是否开启鼠标悬停stop
        direction: 1, // 0向下 1向上 2向左 3向右
        // openWatch: true, // 开启数据实时监控刷新dom
        singleHeight: 0, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
        singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
        waitTime: 1000, // 单步运动停止的时间(默认值1000ms)
      };
    },
  },
  methods: {
    requestTestData() {
      let params = {
        type: this.details.id,
      };
      http.get("/hotfocus/hotFocusQueryPage", { params }).then((res) => {
        this.textDataList = res.data.result;
      });
    },
  },
};
</script>

<style lang="scss" >
.hot-follow-router-container {
  width: 2421px;
  height: 969px;
  box-sizing: border-box;
  .main-header {
    position: relative;
  }
  .hot-follow-router-body {
    margin-top: 34px;
    width: 1131px;
    height: 726px;
    overflow: hidden;
    .content {
      box-sizing: border-box;
      width: 100%;
      height: 88px;

      display: flex;
      justify-content: flex-start;
      align-items: center;
      position: relative;
      .imgLeft {
        width: 35px;
        height: 35px;
        background: url("../../assets/rdgz-icon.png") left center no-repeat;
        background-size: 100% 100%;
      }
      .textRight {
        padding-left: 26px;
        font-size: 28px;
        color: #edf9ff;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      .lineLeft1 {
        position: absolute;
        height: 25px;
        width: 2px;
        background: url("../../assets/line.png") no-repeat;
        top: 64px;
        left: 17px;
      }
      .lineLeft2 {
        position: absolute;
        height: 25px;
        width: 2px;
        background: url("../../assets/line.png") no-repeat;
        top: 0px;
        left: 17px;
      }
    }

    .content:hover {
      .imgLeft {
        width: 44px;
        height: 44px;
        background: url("../../assets/rdgz-icon.png") left center no-repeat;
        background-size: 100% 100%;
      }
      .textRight {
        padding-left: 26px;
        color: #1af2ff;
      }
    }
  }
}
</style>